'use client';

import { useState, useEffect } from 'react';
import { Download, DollarSign, CreditCard, Calendar, Receipt, TrendingUp, BarChart3, PieChart, Users, Activity } from 'lucide-react';
import { FiTrendingUp, FiBarChart, FiUsers, FiActivity, FiPieChart, FiCreditCard } from 'react-icons/fi';
import {
  LineChart,
  Line,
  XAxis,
  YAxis,
  CartesianGrid,
  Tooltip,
  ResponsiveContainer,
  BarChart,
  Bar,
  PieChart as RechartsPieChart,
  Pie,
  Cell,
  Legend
} from 'recharts';

interface Payment {
  _id: string;
  plan: string;
  amount: number;
  status: string;
  userId: string;
  createdAt: string;
}

interface AnalyticsData {
  paymentTrends: any[];
  dailyEarnings: any[];
  planDistribution: any[];
  monthlyRevenue: any[];
  userGrowth: any[];
  topPlans: any[];
}

export default function AnalyticsDashboard() {
  const [analytics, setAnalytics] = useState<AnalyticsData>({
    paymentTrends: [],
    dailyEarnings: [],
    planDistribution: [],
    monthlyRevenue: [],
    userGrowth: [],
    topPlans: []
  });
  const [loading, setLoading] = useState(true);
  const [stats, setStats] = useState({
    totalRevenue: 0,
    totalPayments: 0,
    avgPayment: 0,
    activeUsers: 0
  });

  useEffect(() => {
    fetchAnalytics();
  }, []);

  const fetchAnalytics = async () => {
    try {
      setLoading(true);
      const [analyticsRes, paymentsRes] = await Promise.all([
        fetch('/api/admin/analytics'),
        fetch('/api/admin/payments?limit=1000') // Get all payments for analytics
      ]);

      const analyticsData = await analyticsRes.json();
      const paymentsData = await paymentsRes.json();

      // Process comprehensive analytics
      const processedData = processAnalyticsData(paymentsData.payments || []);
      setAnalytics(processedData);

      // Calculate stats
      const totalRevenue = paymentsData.payments?.reduce((sum: number, p: Payment) => sum + p.amount, 0) || 0;
      const totalPayments = paymentsData.payments?.length || 0;
      const avgPayment = totalPayments > 0 ? Math.round(totalRevenue / totalPayments) : 0;

      setStats({
        totalRevenue,
        totalPayments,
        avgPayment,
        activeUsers: analyticsData.activeUsers || 0
      });
    } catch (error) {
      console.error('Error fetching analytics:', error);
    } finally {
      setLoading(false);
    }
  };

  const processAnalyticsData = (payments: Payment[]): AnalyticsData => {
    // Payment trends over time (last 30 days)
    const trendsMap = new Map();
    const now = new Date();
    const thirtyDaysAgo = new Date(now.getTime() - 30 * 24 * 60 * 60 * 1000);

    payments.forEach(payment => {
      const date = new Date(payment.createdAt).toISOString().split('T')[0];
      if (new Date(payment.createdAt) >= thirtyDaysAgo) {
        trendsMap.set(date, (trendsMap.get(date) || 0) + payment.amount);
      }
    });

    const paymentTrends = Array.from(trendsMap.entries())
      .map(([date, amount]) => ({ date, amount }))
      .sort((a, b) => a.date.localeCompare(b.date));

    // Daily earnings (last 7 days)
    const dailyMap = new Map();
    const sevenDaysAgo = new Date(now.getTime() - 7 * 24 * 60 * 60 * 1000);

    payments.forEach(payment => {
      const date = new Date(payment.createdAt).toISOString().split('T')[0];
      if (new Date(payment.createdAt) >= sevenDaysAgo) {
        dailyMap.set(date, (dailyMap.get(date) || 0) + payment.amount);
      }
    });

    const dailyEarnings = Array.from(dailyMap.entries())
      .map(([date, amount]) => ({ date: new Date(date).toLocaleDateString(), amount }))
      .sort((a, b) => new Date(a.date).getTime() - new Date(b.date).getTime());

    // Plan distribution
    const planMap = new Map();
    payments.forEach(payment => {
      planMap.set(payment.plan, (planMap.get(payment.plan) || 0) + 1);
    });

    const planDistribution = Array.from(planMap.entries()).map(([plan, count]) => ({
      name: plan,
      value: count,
      amount: payments.filter(p => p.plan === plan).reduce((sum, p) => sum + p.amount, 0)
    }));

    // Monthly revenue (last 12 months)
    const monthlyMap = new Map();
    const twelveMonthsAgo = new Date(now.getFullYear(), now.getMonth() - 11, 1);

    payments.forEach(payment => {
      const paymentDate = new Date(payment.createdAt);
      if (paymentDate >= twelveMonthsAgo) {
        const monthKey = `${paymentDate.getFullYear()}-${String(paymentDate.getMonth() + 1).padStart(2, '0')}`;
        monthlyMap.set(monthKey, (monthlyMap.get(monthKey) || 0) + payment.amount);
      }
    });

    const monthlyRevenue = Array.from(monthlyMap.entries())
      .map(([month, amount]) => ({
        month: new Date(month + '-01').toLocaleDateString('en-US', { month: 'short', year: 'numeric' }),
        amount
      }))
      .sort((a, b) => new Date(a.month).getTime() - new Date(b.month).getTime());

    // User growth (simplified - would need user registration data)
    const userGrowth = [
      { month: 'Jan', users: 120 },
      { month: 'Feb', users: 150 },
      { month: 'Mar', users: 180 },
      { month: 'Apr', users: 220 },
      { month: 'May', users: 280 },
      { month: 'Jun', users: 320 }
    ];

    // Top plans
    const topPlans = planDistribution
      .sort((a, b) => b.amount - a.amount)
      .slice(0, 5);

    return {
      paymentTrends,
      dailyEarnings,
      planDistribution,
      monthlyRevenue,
      userGrowth,
      topPlans
    };
  };

  if (loading) {
    return (
      <div className="bg-white dark:bg-gray-800 rounded-lg shadow p-6">
        <div className="flex justify-center items-center h-64">
          <div className="animate-spin rounded-full h-8 w-8 border-b-2 border-blue-600"></div>
        </div>
      </div>
    );
  }

  return (
    <div className="space-y-8">
      {/* Key Metrics */}
      <div className="grid grid-cols-1 md:grid-cols-4 gap-6">
        <div className="bg-white dark:bg-gray-800 rounded-lg shadow p-6">
          <div className="flex items-center">
            <div className="p-3 bg-green-100 dark:bg-green-900 rounded-lg">
              <DollarSign className="w-6 h-6 text-green-600 dark:text-green-400" />
            </div>
            <div className="ml-4">
              <p className="text-sm font-medium text-gray-600 dark:text-gray-400">Total Revenue</p>
              <p className="text-2xl font-bold text-gray-900 dark:text-white">${stats.totalRevenue}</p>
            </div>
          </div>
        </div>

        <div className="bg-white dark:bg-gray-800 rounded-lg shadow p-6">
          <div className="flex items-center">
            <div className="p-3 bg-blue-100 dark:bg-blue-900 rounded-lg">
              <Receipt className="w-6 h-6 text-blue-600 dark:text-blue-400" />
            </div>
            <div className="ml-4">
              <p className="text-sm font-medium text-gray-600 dark:text-gray-400">Total Payments</p>
              <p className="text-2xl font-bold text-gray-900 dark:text-white">{stats.totalPayments}</p>
            </div>
          </div>
        </div>

        <div className="bg-white dark:bg-gray-800 rounded-lg shadow p-6">
          <div className="flex items-center">
            <div className="p-3 bg-purple-100 dark:bg-purple-900 rounded-lg">
              <TrendingUp className="w-6 h-6 text-purple-600 dark:text-purple-400" />
            </div>
            <div className="ml-4">
              <p className="text-sm font-medium text-gray-600 dark:text-gray-400">Avg Payment</p>
              <p className="text-2xl font-bold text-gray-900 dark:text-white">${stats.avgPayment}</p>
            </div>
          </div>
        </div>

        <div className="bg-white dark:bg-gray-800 rounded-lg shadow p-6">
          <div className="flex items-center">
            <div className="p-3 bg-orange-100 dark:bg-orange-900 rounded-lg">
              <Users className="w-6 h-6 text-orange-600 dark:text-orange-400" />
            </div>
            <div className="ml-4">
              <p className="text-sm font-medium text-gray-600 dark:text-gray-400">Active Users</p>
              <p className="text-2xl font-bold text-gray-900 dark:text-white">{stats.activeUsers}</p>
            </div>
          </div>
        </div>
      </div>

      {/* Analytics Charts */}
      <div className="space-y-6">
        <h3 className="text-xl font-semibold text-gray-900 dark:text-white flex items-center gap-2">
          <BarChart3 className="w-5 h-5 text-blue-500" />
          Payment Analytics
        </h3>

        {/* Charts Grid */}
        <div className="grid grid-cols-1 lg:grid-cols-2 gap-6">
          {/* Payment Trends */}
          <div className="bg-white dark:bg-gray-800 rounded-lg shadow p-6">
            <h4 className="text-lg font-semibold text-gray-900 dark:text-white mb-4 flex items-center gap-2">
              <TrendingUp className="w-4 h-4 text-green-500" />
              Payment Trends (Last 30 Days)
            </h4>
            <div className="h-64">
              <ResponsiveContainer width="100%" height="100%">
                <LineChart data={analytics.paymentTrends}>
                  <CartesianGrid strokeDasharray="3 3" stroke="#374151" />
                  <XAxis
                    dataKey="date"
                    stroke="#6B7280"
                    fontSize={12}
                    tickFormatter={(value) => new Date(value).toLocaleDateString('en-US', { month: 'short', day: 'numeric' })}
                  />
                  <YAxis stroke="#6B7280" fontSize={12} />
                  <Tooltip
                    contentStyle={{
                      backgroundColor: '#1F2937',
                      border: '1px solid #374151',
                      borderRadius: '8px',
                      color: '#F9FAFB'
                    }}
                    formatter={(value: any) => [`$${value}`, 'Amount']}
                    labelFormatter={(value) => new Date(value).toLocaleDateString()}
                  />
                  <Line
                    type="monotone"
                    dataKey="amount"
                    stroke="#10B981"
                    strokeWidth={2}
                    dot={{ fill: '#10B981', strokeWidth: 2, r: 4 }}
                  />
                </LineChart>
              </ResponsiveContainer>
            </div>
          </div>

          {/* Daily Earnings */}
          <div className="bg-white dark:bg-gray-800 rounded-lg shadow p-6">
            <h4 className="text-lg font-semibold text-gray-900 dark:text-white mb-4 flex items-center gap-2">
              <BarChart3 className="w-4 h-4 text-blue-500" />
              Daily Earnings (Last 7 Days)
            </h4>
            <div className="h-64">
              <ResponsiveContainer width="100%" height="100%">
                <BarChart data={analytics.dailyEarnings}>
                  <CartesianGrid strokeDasharray="3 3" stroke="#374151" />
                  <XAxis
                    dataKey="date"
                    stroke="#6B7280"
                    fontSize={12}
                    angle={-45}
                    textAnchor="end"
                    height={60}
                  />
                  <YAxis stroke="#6B7280" fontSize={12} />
                  <Tooltip
                    contentStyle={{
                      backgroundColor: '#1F2937',
                      border: '1px solid #374151',
                      borderRadius: '8px',
                      color: '#F9FAFB'
                    }}
                    formatter={(value: any) => [`$${value}`, 'Earnings']}
                  />
                  <Bar dataKey="amount" fill="#3B82F6" radius={[4, 4, 0, 0]} />
                </BarChart>
              </ResponsiveContainer>
            </div>
          </div>

          {/* Plan Distribution */}
          <div className="bg-white dark:bg-gray-800 rounded-lg shadow p-6">
            <h4 className="text-lg font-semibold text-gray-900 dark:text-white mb-4 flex items-center gap-2">
              <PieChart className="w-4 h-4 text-purple-500" />
              Plan Distribution
            </h4>
            <div className="h-64">
              <ResponsiveContainer width="100%" height="100%">
                <RechartsPieChart>
                  <Pie
                    data={analytics.planDistribution}
                    cx="50%"
                    cy="50%"
                    innerRadius={40}
                    outerRadius={80}
                    paddingAngle={5}
                    dataKey="value"
                  >
                    {analytics.planDistribution.map((entry, index) => (
                      <Cell key={`cell-${index}`} fill={['#10B981', '#3B82F6', '#8B5CF6', '#F59E0B'][index % 4]} />
                    ))}
                  </Pie>
                  <Tooltip
                    contentStyle={{
                      backgroundColor: '#1F2937',
                      border: '1px solid #374151',
                      borderRadius: '8px',
                      color: '#F9FAFB'
                    }}
                    formatter={(value: any, name: any, props: any) => [
                      `${value} payments ($${props.payload.amount})`,
                      props.payload.name
                    ]}
                  />
                  <Legend
                    wrapperStyle={{ color: '#F9FAFB' }}
                    formatter={(value, entry: any) => `${entry.payload.name} (${entry.payload.value})`}
                  />
                </RechartsPieChart>
              </ResponsiveContainer>
            </div>
          </div>

          {/* Monthly Revenue */}
          <div className="bg-white dark:bg-gray-800 rounded-lg shadow p-6">
            <h4 className="text-lg font-semibold text-gray-900 dark:text-white mb-4 flex items-center gap-2">
              <DollarSign className="w-4 h-4 text-green-500" />
              Monthly Revenue (Last 12 Months)
            </h4>
            <div className="h-64">
              <ResponsiveContainer width="100%" height="100%">
                <BarChart data={analytics.monthlyRevenue}>
                  <CartesianGrid strokeDasharray="3 3" stroke="#374151" />
                  <XAxis
                    dataKey="month"
                    stroke="#6B7280"
                    fontSize={12}
                    angle={-45}
                    textAnchor="end"
                    height={60}
                  />
                  <YAxis stroke="#6B7280" fontSize={12} />
                  <Tooltip
                    contentStyle={{
                      backgroundColor: '#1F2937',
                      border: '1px solid #374151',
                      borderRadius: '8px',
                      color: '#F9FAFB'
                    }}
                    formatter={(value: any) => [`$${value}`, 'Revenue']}
                  />
                  <Bar dataKey="amount" fill="#10B981" radius={[4, 4, 0, 0]} />
                </BarChart>
              </ResponsiveContainer>
            </div>
          </div>
        </div>

        {/* Additional Analytics */}
        <div className="grid grid-cols-1 lg:grid-cols-2 gap-6">
          {/* User Growth */}
          <div className="bg-white dark:bg-gray-800 rounded-lg shadow p-6">
            <h4 className="text-lg font-semibold text-gray-900 dark:text-white mb-4 flex items-center gap-2">
              <Users className="w-4 h-4 text-indigo-500" />
              User Growth
            </h4>
            <div className="h-64">
              <ResponsiveContainer width="100%" height="100%">
                <LineChart data={analytics.userGrowth}>
                  <CartesianGrid strokeDasharray="3 3" stroke="#374151" />
                  <XAxis dataKey="month" stroke="#6B7280" fontSize={12} />
                  <YAxis stroke="#6B7280" fontSize={12} />
                  <Tooltip
                    contentStyle={{
                      backgroundColor: '#1F2937',
                      border: '1px solid #374151',
                      borderRadius: '8px',
                      color: '#F9FAFB'
                    }}
                    formatter={(value: any) => [value, 'Users']}
                  />
                  <Line
                    type="monotone"
                    dataKey="users"
                    stroke="#6366F1"
                    strokeWidth={2}
                    dot={{ fill: '#6366F1', strokeWidth: 2, r: 4 }}
                  />
                </LineChart>
              </ResponsiveContainer>
            </div>
          </div>

          {/* Top Plans */}
          <div className="bg-white dark:bg-gray-800 rounded-lg shadow p-6">
            <h4 className="text-lg font-semibold text-gray-900 dark:text-white mb-4 flex items-center gap-2">
              <Activity className="w-4 h-4 text-pink-500" />
              Top Performing Plans
            </h4>
            <div className="space-y-4">
              {analytics.topPlans.map((plan, index) => (
                <div key={plan.name} className="flex items-center justify-between p-3 bg-gray-50 dark:bg-gray-700 rounded-lg">
                  <div className="flex items-center gap-3">
                    <div className={`w-8 h-8 rounded-full flex items-center justify-center text-sm font-bold ${
                      index === 0 ? 'bg-yellow-100 text-yellow-800' :
                      index === 1 ? 'bg-gray-100 text-gray-800' :
                      index === 2 ? 'bg-orange-100 text-orange-800' :
                      'bg-blue-100 text-blue-800'
                    }`}>
                      {index + 1}
                    </div>
                    <div>
                      <p className="font-medium text-gray-900 dark:text-white">{plan.name}</p>
                      <p className="text-sm text-gray-600 dark:text-gray-400">{plan.value} payments</p>
                    </div>
                  </div>
                  <div className="text-right">
                    <p className="font-bold text-gray-900 dark:text-white">${plan.amount}</p>
                  </div>
                </div>
              ))}
            </div>
          </div>
        </div>

        {/* Summary Stats */}
        <div className="grid grid-cols-1 md:grid-cols-3 gap-6">
          <div className="bg-white dark:bg-gray-800 rounded-lg shadow p-6">
            <div className="flex items-center gap-3">
              <div className="p-3 bg-indigo-100 dark:bg-indigo-900 rounded-lg">
                <TrendingUp className="w-6 h-6 text-indigo-600 dark:text-indigo-400" />
              </div>
              <div>
                <p className="text-sm font-medium text-gray-600 dark:text-gray-400">Revenue Growth</p>
                <p className="text-2xl font-bold text-gray-900 dark:text-white">
                  {analytics.paymentTrends.length > 1
                    ? `${(((analytics.paymentTrends[analytics.paymentTrends.length - 1]?.amount || 0) - (analytics.paymentTrends[0]?.amount || 0)) / Math.max(analytics.paymentTrends[0]?.amount || 1, 1) * 100).toFixed(1)}%`
                    : 'N/A'
                  }
                </p>
              </div>
            </div>
          </div>

          <div className="bg-white dark:bg-gray-800 rounded-lg shadow p-6">
            <div className="flex items-center gap-3">
              <div className="p-3 bg-emerald-100 dark:bg-emerald-900 rounded-lg">
                <Calendar className="w-6 h-6 text-emerald-600 dark:text-emerald-400" />
              </div>
              <div>
                <p className="text-sm font-medium text-gray-600 dark:text-gray-400">Avg Monthly Revenue</p>
                <p className="text-2xl font-bold text-gray-900 dark:text-white">
                  ${analytics.monthlyRevenue.length > 0
                    ? Math.round(analytics.monthlyRevenue.reduce((sum, m) => sum + m.amount, 0) / analytics.monthlyRevenue.length)
                    : 0
                  }
                </p>
              </div>
            </div>
          </div>

          <div className="bg-white dark:bg-gray-800 rounded-lg shadow p-6">
            <div className="flex items-center gap-3">
              <div className="p-3 bg-rose-100 dark:bg-rose-900 rounded-lg">
                <CreditCard className="w-6 h-6 text-rose-600 dark:text-rose-400" />
              </div>
              <div>
                <p className="text-sm font-medium text-gray-600 dark:text-gray-400">Conversion Rate</p>
                <p className="text-2xl font-bold text-gray-900 dark:text-white">
                  {stats.activeUsers > 0 ? ((stats.totalPayments / stats.activeUsers) * 100).toFixed(1) : 0}%
                </p>
              </div>
            </div>
          </div>
        </div>
      </div>

      {/* Revenue Trends Chart */}
      <div className="bg-white dark:bg-gray-800 rounded-lg shadow p-6">
        <div className="flex items-center justify-between mb-4">
          <h3 className="text-lg font-semibold text-gray-900 dark:text-white flex items-center">
            <FiTrendingUp className="mr-2" size={20} />
            Revenue Trends
          </h3>
          <select className="px-3 py-1 border border-gray-300 dark:border-gray-600 rounded-md dark:bg-gray-700 dark:text-white">
            <option>Last 30 days</option>
            <option>Last 90 days</option>
            <option>Last year</option>
          </select>
        </div>
        <div className="h-64 flex items-center justify-center text-gray-500 dark:text-gray-400">
          <div className="text-center">
            <FiBarChart className="mx-auto mb-2 opacity-50" />
            <p>Revenue chart visualization</p>
            <p className="text-sm">Chart library integration needed</p>
          </div>
        </div>
      </div>

      {/* User Growth Chart */}
      <div className="bg-white dark:bg-gray-800 rounded-lg shadow p-6">
        <div className="flex items-center justify-between mb-4">
          <h3 className="text-lg font-semibold text-gray-900 dark:text-white flex items-center">
            <FiUsers className="mr-2" size={20} />
            User Growth
          </h3>
        </div>
        <div className="h-64 flex items-center justify-center text-gray-500 dark:text-gray-400">
          <div className="text-center">
            <FiActivity size={48} className="mx-auto mb-2 opacity-50" />
            <p>User growth chart</p>
            <p className="text-sm">Chart library integration needed</p>
          </div>
        </div>
      </div>

      <div className="grid grid-cols-1 lg:grid-cols-2 gap-6">
        {/* Plan Distribution Pie Chart */}
        <div className="bg-white dark:bg-gray-800 rounded-lg shadow p-6">
          <div className="flex items-center justify-between mb-4">
            <h3 className="text-lg font-semibold text-gray-900 dark:text-white flex items-center">
              <FiPieChart className="mr-2" size={20} />
              Subscription Plans
            </h3>
          </div>
          <div className="h-64 flex items-center justify-center text-gray-500 dark:text-gray-400">
            <div className="text-center">
              <FiPieChart size={48} className="mx-auto mb-2 opacity-50" />
              <p>Plan distribution pie chart</p>
              <p className="text-sm">Chart library integration needed</p>
            </div>
          </div>
          {/* Mock data display */}
          <div className="mt-4 space-y-2">
            <div className="flex justify-between items-center">
              <span className="text-sm text-gray-600 dark:text-gray-400">Free</span>
              <span className="text-sm font-medium text-gray-900 dark:text-white">65%</span>
            </div>
            <div className="w-full bg-gray-200 dark:bg-gray-700 rounded-full h-2">
              <div className="bg-blue-600 h-2 rounded-full" style={{ width: '65%' }}></div>
            </div>
            <div className="flex justify-between items-center">
              <span className="text-sm text-gray-600 dark:text-gray-400">1 Month</span>
              <span className="text-sm font-medium text-gray-900 dark:text-white">20%</span>
            </div>
            <div className="w-full bg-gray-200 dark:bg-gray-700 rounded-full h-2">
              <div className="bg-green-600 h-2 rounded-full" style={{ width: '20%' }}></div>
            </div>
            <div className="flex justify-between items-center">
              <span className="text-sm text-gray-600 dark:text-gray-400">6 Months</span>
              <span className="text-sm font-medium text-gray-900 dark:text-white">10%</span>
            </div>
            <div className="w-full bg-gray-200 dark:bg-gray-700 rounded-full h-2">
              <div className="bg-purple-600 h-2 rounded-full" style={{ width: '10%' }}></div>
            </div>
            <div className="flex justify-between items-center">
              <span className="text-sm text-gray-600 dark:text-gray-400">12 Months</span>
              <span className="text-sm font-medium text-gray-900 dark:text-white">5%</span>
            </div>
            <div className="w-full bg-gray-200 dark:bg-gray-700 rounded-full h-2">
              <div className="bg-orange-600 h-2 rounded-full" style={{ width: '5%' }}></div>
            </div>
          </div>
        </div>

        {/* Top Tools Usage */}
        <div className="bg-white dark:bg-gray-800 rounded-lg shadow p-6">
          <div className="flex items-center justify-between mb-4">
            <h3 className="text-lg font-semibold text-gray-900 dark:text-white flex items-center">
              <FiBarChart className="mr-2" size={20} />
              Top Tools Usage
            </h3>
          </div>
          <div className="space-y-4">
            {[
              { name: 'Code Analyzer', usage: 1250, percentage: 35 },
              { name: 'Data Processor', usage: 980, percentage: 28 },
              { name: 'File Converter', usage: 750, percentage: 21 },
              { name: 'Text Formatter', usage: 420, percentage: 12 },
              { name: 'Image Optimizer', usage: 180, percentage: 4 },
            ].map((tool, index) => (
              <div key={tool.name} className="flex items-center justify-between">
                <div className="flex items-center">
                  <div className={`w-8 h-8 rounded-full flex items-center justify-center text-white text-sm font-bold mr-3 ${
                    ['bg-blue-500', 'bg-green-500', 'bg-purple-500', 'bg-orange-500', 'bg-red-500'][index]
                  }`}>
                    {index + 1}
                  </div>
                  <div>
                    <div className="text-sm font-medium text-gray-900 dark:text-white">{tool.name}</div>
                    <div className="text-xs text-gray-500 dark:text-gray-400">{tool.usage} uses</div>
                  </div>
                </div>
                <div className="text-right">
                  <div className="text-sm font-medium text-gray-900 dark:text-white">{tool.percentage}%</div>
                  <div className="w-16 bg-gray-200 dark:bg-gray-700 rounded-full h-1 mt-1">
                    <div
                      className="bg-blue-600 h-1 rounded-full"
                      style={{ width: `${tool.percentage}%` }}
                    ></div>
                  </div>
                </div>
              </div>
            ))}
          </div>
        </div>
      </div>

      {/* Activity Heatmap */}
      <div className="bg-white dark:bg-gray-800 rounded-lg shadow p-6">
        <div className="flex items-center justify-between mb-4">
          <h3 className="text-lg font-semibold text-gray-900 dark:text-white flex items-center">
            <FiActivity className="mr-2" size={20} />
            Daily Activity Heatmap
          </h3>
        </div>
        <div className="h-64 flex items-center justify-center text-gray-500 dark:text-gray-400">
          <div className="text-center">
            <FiActivity size={48} className="mx-auto mb-2 opacity-50" />
            <p>Activity heatmap visualization</p>
            <p className="text-sm">Chart library integration needed</p>
          </div>
        </div>
        {/* Mock heatmap grid */}
        <div className="mt-4 grid grid-cols-7 gap-1">
          {Array.from({ length: 35 }, (_, i) => (
            <div
              key={i}
              className={`w-3 h-3 rounded-sm ${
                Math.random() > 0.7
                  ? 'bg-green-400'
                  : Math.random() > 0.4
                  ? 'bg-green-300'
                  : Math.random() > 0.2
                  ? 'bg-green-200'
                  : 'bg-gray-200 dark:bg-gray-700'
              }`}
            ></div>
          ))}
        </div>
        <div className="mt-2 flex justify-between text-xs text-gray-500 dark:text-gray-400">
          <span>Less</span>
          <span>More</span>
        </div>
      </div>

      {/* Key Metrics */}
      <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-4 gap-6">
        <div className="bg-white dark:bg-gray-800 rounded-lg shadow p-6">
          <div className="flex items-center">
            <div className="p-3 bg-blue-100 dark:bg-blue-900 rounded-lg">
              <FiUsers className="text-blue-600 dark:text-blue-400" size={24} />
            </div>
            <div className="ml-4">
              <p className="text-sm font-medium text-gray-600 dark:text-gray-400">Monthly Active Users</p>
              <p className="text-2xl font-bold text-gray-900 dark:text-white">2,847</p>
              <p className="text-xs text-green-600 dark:text-green-400">+12.5% from last month</p>
            </div>
          </div>
        </div>

        <div className="bg-white dark:bg-gray-800 rounded-lg shadow p-6">
          <div className="flex items-center">
            <div className="p-3 bg-green-100 dark:bg-green-900 rounded-lg">
              <FiCreditCard className="text-green-600 dark:text-green-400" size={24} />
            </div>
            <div className="ml-4">
              <p className="text-sm font-medium text-gray-600 dark:text-gray-400">Conversion Rate</p>
              <p className="text-2xl font-bold text-gray-900 dark:text-white">3.2%</p>
              <p className="text-xs text-green-600 dark:text-green-400">+0.8% from last month</p>
            </div>
          </div>
        </div>

        <div className="bg-white dark:bg-gray-800 rounded-lg shadow p-6">
          <div className="flex items-center">
            <div className="p-3 bg-purple-100 dark:bg-purple-900 rounded-lg">
              <FiTrendingUp className="text-purple-600 dark:text-purple-400" size={24} />
            </div>
            <div className="ml-4">
              <p className="text-sm font-medium text-gray-600 dark:text-gray-400">Avg. Session Time</p>
              <p className="text-2xl font-bold text-gray-900 dark:text-white">8m 32s</p>
              <p className="text-xs text-red-600 dark:text-red-400">-2.1% from last month</p>
            </div>
          </div>
        </div>

        <div className="bg-white dark:bg-gray-800 rounded-lg shadow p-6">
          <div className="flex items-center">
            <div className="p-3 bg-orange-100 dark:bg-orange-900 rounded-lg">
              <FiBarChart3 className="text-orange-600 dark:text-orange-400" size={24} />
            </div>
            <div className="ml-4">
              <p className="text-sm font-medium text-gray-600 dark:text-gray-400">Tool Usage</p>
              <p className="text-2xl font-bold text-gray-900 dark:text-white">15,420</p>
              <p className="text-xs text-green-600 dark:text-green-400">+18.7% from last month</p>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
}